import PropTypes from 'prop-types';
import React from 'react';

import Navbar from 'react-bootstrap/Navbar';
import Nav from 'react-bootstrap/Nav';
import Tooltip from 'react-bootstrap/Tooltip';
import OverlayTrigger from 'react-bootstrap/OverlayTrigger';
import styled from 'astroturf';
import withProps from 'recompose/withProps';

import logo from '../assets/logo.svg';

const StyledNavbar = withProps({
  as: 'header',
  variant: 'dark',
  role: 'banner',
})(
  styled(Navbar, null, "StyledNavbar", require("./NavMain-StyledNavbar.module.scss"), "styled-navbar", "styledNavbar"),
);
const SkipToContentLink = styled('a', null, "SkipToContentLink", require("./NavMain-SkipToContentLink.module.scss"), "skip-to-content-link", "skipToContentLink");

const StyledNavLink = styled(Nav.Link, null, "StyledNavLink", require("./NavMain-StyledNavLink.module.scss"), "styled-nav-link", "styledNavLink");

const NAV_LINKS = [
  {
    link: '/',
    title: 'Home',
    exact: true,
  },
  {
    link: '/getting-started/introduction',
    title: 'Getting Started',
  },
  {
    link: '/components/alerts',
    title: 'Components',
  },
];

const propTypes = {
  activePage: PropTypes.string,
};

function NavMain({ activePage }) {
  return (
    <StyledNavbar expand collapseOnSelect>
      <SkipToContentLink href="#rb-docs-content" tabIndex="0">
        Skip to content
      </SkipToContentLink>
      <Navbar.Brand href="/">
        <img src={logo} alt="react-bootstrap" height={30} />
      </Navbar.Brand>

      <Nav role="navigation" id="top" className="d-none d-md-flex">
        {NAV_LINKS.map(({ link, title, exact }) => (
          <StyledNavLink
            key={link}
            href={link}
            active={exact ? activePage === link : activePage.startsWith(link)}
          >
            {title}
          </StyledNavLink>
        ))}
      </Nav>
      <Nav className="ml-auto pr-md-5">
        <OverlayTrigger
          placement="bottom"
          delay={{ show: 200 }}
          overlay={
            <Tooltip id="t-version">
              Compatible with Bootstrap v
              {config.bootstrapVersion
                .split('.')
                .slice(0, 2)
                .join('.')}
            </Tooltip>
          }
        >
          <StyledNavLink>v{config.version}</StyledNavLink>
        </OverlayTrigger>
        <OverlayTrigger
          placement="bottom"
          delay={{ show: 200 }}
          overlay={<Tooltip id="t-github">Github</Tooltip>}
        >
          <StyledNavLink
            href="https://github.com/react-bootstrap/react-bootstrap"
            target="_blank"
            rel="noopener noreferrer"
          >
            <i className="fab fa-github" />
            <span className="sr-only">Github</span>
          </StyledNavLink>
        </OverlayTrigger>
        <OverlayTrigger
          placement="bottom"
          delay={{ show: 200 }}
          overlay={<Tooltip id="t-discord">Discord</Tooltip>}
        >
          <StyledNavLink
            href="https://discord.gg/0ZcbPKXt5bXLs9XK"
            target="_blank"
            rel="noopener noreferrer"
          >
            <i className="fab fa-discord" />
            <span className="sr-only">Discord</span>
          </StyledNavLink>
        </OverlayTrigger>
      </Nav>
    </StyledNavbar>
  );
}

NavMain.propTypes = propTypes;

export default NavMain;
